<template>
  <icon
    class="umis-icon__container"
    :class="[{ invert: invert }, classname]"
    :icon="icon"
    :style="{
      width: `${size}px`,
      height: `${size}px`,
      fontSize: `${size}px`,
      verticalAlign: 'middle',
      '--color': color,
      '--bgColor': bgColor,
      borderRadius: `${borderRadius}px`,
    }"
    @click="handleClick"
  />
</template>

<script>
import {defineComponent, createVNode} from 'vue';
import * as Icons from '@element-plus/icons-vue/dist/lib';
import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import mixinProps from '../props/icon';

const Icon = props => createVNode(Icons[props.icon]);

export default defineComponent({
  name: 'MisIcon',
  components: {Icon},
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const state = useInitData(props);
    const handleClick = () => {
      props.actionType
        && props.action({
          actionType: props.actionType,
          ...ctx.attrs,
        }, props.initData);
    };

    return {
      handleClick,
      state
    };
  },
});
</script>
