<template>
  <div class="umis-video__container">
    <video-player
      :poster="poster"
      :src="src"
      :auto-play="autoplay"
      :volume="volume"
      :width="width"
      :height="height"
    />
  </div>
</template>
<script>
import {defineComponent, defineAsyncComponent, onMounted} from 'vue';
import {ElLoading, ElResult} from 'element-plus';
import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import mixinProps from '../props/video';

export default defineComponent({
  name: 'MisVideo',
  components: {
    VideoPlayer: defineAsyncComponent({
      loader: () => import(/* webpackChunkName: "video-player", webpackPrefetch: true */ 'vue3-video-play'),
      loadingComponent: ElLoading,
      errorComponent: ElResult,
      delay: 200,
      timeout: 6000
    }),
  },
  mixins: [mixinProps, initData],
  setup(props) {
    const state = useInitData(props);
    onMounted(() => {
      import(/* webpackChunkName: "video-player.css", webpackPrefetch: true */ '!style-loader!css-loader!vue3-video-play/dist/style.css');
    });
    return {
      ...useDerivedProps(props),
      state,
    };
  },
});
</script>
