<template>
  <div v-if="bodyItem" class="umis-mapping__container">
    <mis-component
      v-bind="iAttrs(bodyItem)"
      :mis-name="bodyItem.renderer"
      :path="iPath"
      :body="iBody(bodyItem)"
      :header="iHeader(bodyItem)"
      :footer="iFooter(bodyItem)"
      :props="iAttrs(bodyItem)"
      :init-data="getData()"
      class="umis-mapping__container"
    />
  </div>
  <span
    v-else-if="!bodyItem && inline"
    v-html="getMapItem"
    class="umis-mapping__container"
  />
  <div
    v-else-if="!bodyItem && !inline"
    v-html="getMapItem"
    class="umis-mapping__container"
  />
</template>
<script>
import {defineComponent, getCurrentInstance, computed, onMounted, ref} from 'vue';
import {clonedeep} from 'lodash';
import useInitApi from '../../hooks/useInitApi';
import useInitData from '../../hooks/useInitData';
import useDerivedProps from '../../hooks/useDerivedProps';
import initData from '../props/init-data';
import initApi from '../props/init-api';
import mixinProps from '../props/mapping';

export default defineComponent({
  name: 'MisMapping',
  mixins: [mixinProps, initData, initApi],
  setup(props) {
    const { proxy } = getCurrentInstance();
    const { handleFetchApi, shouldComponentUpdate } = useInitApi(props);
    const state = useInitData(props);
    const getMapItem = computed(() => {
      if (props.initApi && state.data.cachedMap) {
        const key = proxy.$renderTpl(props.value, state.data);
        return state.data.cachedMap[key];
      }
      if (props.map) {
        const key = proxy.$renderTpl(props.value, state.data);
        return props.map[key];
      }
      return '';
    });
    const onComponentUpdate = () => {
      if (props.initApi) {
        fetchDataMap();
      }
    };
    const fetchDataMap = () => {
      handleFetchApi(props.initApi, state.data)
        .then((result) => {
          state.data.cachedMap = result;
        });
    };
    const getData = () => {
      const inherits = {};
      const data = clonedeep(state.data[props.value]);
      props.distributes.forEach(key => {
        inherits[key] = state.data[key];
      });
      if (props.distributes && Object.prototype.toString.call(data) === '[object Array]') {
        data.forEach(item => {
          Object.assign(item, inherits);
        });
      } else if (props.distributes && Object.prototype.toString.call(data) === '[object Object]') {
        Object.assign(data, inherits)
      }
      return data;
    };
    const bodyItem = ref('');
    const iPath = ref('');
    if (Object.prototype.toString.call(props.body) === '[object Array]') {
      bodyItem.value = props.body[0];
      iPath.value = `${props.path}/body/0`;
    } else if (Object.prototype.toString.call(props.body) === '[object Object]') {
      bodyItem.value = props.body;
      iPath.value = `${props.path}/body`;
    }
    onMounted(() => {
      onComponentUpdate();
    });

    return {
      ...useInitData(props),
      ...useDerivedProps(props),
      shouldComponentUpdate,
      getData,
      getMapItem,
      bodyItem,
      iPath
    };
  },
});
</script>
