<template>
  <div
    :class="classname"
    class="umis-each__container"
  >
    <div
      v-for="(dataItem, index) in state.data"
      :key="`${path}/${index}`"
      :track="`${path}/mis-each`"
      track-id="mis-each"
      class="umis-each__container__item"
    >
      <template v-if="Object.prototype.toString.call(body) === '[object Array]'">
        <template
          v-for="(item, count) in body"
          :key="`${path}/${index}/${item.renderer}/${count}`"
        >
          <mis-component
            :mis-name="item.renderer"
            :path="`${path}/body/${count}/${item.renderer}`"
            :props="iAttrs(item)"
            :header="iHeader(item)"
            :body="iBody(item)"
            :footer="iFooter(item)"
            :init-data="dataItem"
          />
        </template>
      </template>
      <mis-component
        v-else
        v-bind="iAttrs(body)"
        :mis-name="body.renderer"
        :path="`${path}/body/${body.renderer}`"
        :header="iHeader(body)"
        :body="iBody(body)"
        :footer="iFooter(body)"
        :init-data="dataItem"
      />
    </div>
  </div>
</template>

<script>
import { defineComponent } from 'vue';

import useInitData from '../../hooks/useInitData';
import useDerivedProps from '../../hooks/useDerivedProps';
import initData from '../props/init-data';
import mixinProps from '../props/each';

export default defineComponent({
  name: 'MisEach',
  mixins: [mixinProps, initData],
  setup(props) {
    const state = useInitData(props);

    return {
      ...useDerivedProps(props),
      state,
    };
  },
});
</script>
