<template>
  <div
    v-loading="loading && !silentLoading"
    :class="classname"
    class="umis-service__body"
  >
    <template v-if="!error">
      <template v-if="Object.prototype.toString.call(body) === '[object Array]'">
        <template
          v-for="(child, index) in body"
          :key="`${path}/${index}/${child.renderer}`"
        >
          <mis-component
            v-bind="iAttrs(child)"
            :mis-name="child.renderer"
            :path="`${path}/body/${index}/${child.renderer}`"
            :header="iHeader(child)"
            :body="iBody(child)"
            :footer="iFooter(child)"
            :props="iAttrs(child)"
            :init-data="state.data"
          />
        </template>
      </template>
      <mis-component
        v-else-if="Object.prototype.toString.call(body) === '[object Object]'"
        v-bind="iAttrs(body)"
        :path="`${path}/body/${body.renderer}`"
        :mis-name="body.renderer"
        :header="iHeader(body)"
        :body="iBody(body)"
        :footer="iFooter(body)"
        :props="iAttrs(body)"
        :init-data="state.data"
      />
    </template>
    <el-result
      v-else
      icon="error"
      title=""
    >
      <template #extra>
        <el-button
          type="primary"
          size="mini"
          @click="onComponentUpdate"
        >
          
        </el-button>
      </template>
    </el-result>
  </div>
</template>

<script>
import {defineComponent, getCurrentInstance, onMounted, onBeforeUnmount, ref} from 'vue';
import {ElResult} from 'element-plus';
import useDerivedProps from '../../hooks/useDerivedProps';
import useInitApi from '../../hooks/useInitApi';
import useInitData from '../../hooks/useInitData';
import useLinkage from '../../hooks/useLinkage';
import initData from '../props/init-data';
import initApi from '../props/init-api';
import linkage from '../props/linkage';
import mixinProps from '../props/service';

export default defineComponent({
  name: 'MisService',
  components: {
    ElResult
  },
  mixins: [mixinProps, linkage, initApi, initData],
  setup(props) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const { handleFetchApi, shouldComponentUpdate } = useInitApi(props);
    const { onLinkageTrigger } = useLinkage(props);
    const loading = ref(false);
    const error = ref(false);
    let intervalFetch = null;
    let loopCount = 1;

    const handleIntervalApi = () => {
      intervalFetch = setTimeout(() => {
        fetchDataList();
      }, props.interval);
    };

    const onComponentUpdate = () => {
      if (props.initApi && !intervalFetch) {
        fetchDataList();
      }
    };

    const fetchDataList = () => {
      if (proxy && proxy.axiosCancelToken) {
        typeof proxy.axiosCancelToken === 'function' && proxy.axiosCancelToken('CANCELED_DUE_TO_NEW_REQUEST');
      }
      const apiData = props.initApi;

      if (props.interval && props.timeout) {
        if (props.interval * loopCount >= props.timeout) {
          clearTimeout(intervalFetch);
          intervalFetch = null;
          proxy.$notice.error({
            title: '',
            customClass: 'umis-renderer__api-notice',
            dangerouslyUseHTMLString: true,
            message: `<div class="umis-singleline-ellipsis">${props.initApi.url}</div><div></div>`,
          });
          if (props.reload) {
            proxy.$eventHub.$emit('mis-component:reload', props.reload, state.data);
          }
          return;
        }
      }
      if (props.sendOn && !proxy.$expressionEval(props.sendOn, state.data)) {
        return Promise.resolve();
      }

      loading.value = true;
      handleFetchApi(apiData, state.data)
        .then((result) => {
          if (result) {
            if (props.interval && props.timeout) {
              loopCount++;
            }
            proxy.state.data = {...proxy.state.data, ...result};
            loading.value = false;
            if (props.reload) {
              proxy.$eventHub.$emit('mis-component:reload', props.reload, state.data);
            }
            if (props.target) {
              state.data.timestamp = new Date().getTime();
              onLinkageTrigger(props.target, state.data);
            }
            if (props.stopAutoRefreshWhen) {
              if (proxy.$expressionEval(props.stopAutoRefreshWhen, state.data)) {
                clearTimeout(intervalFetch);
                intervalFetch = null;
              } else {
                handleIntervalApi();
              }
            } else if (props.interval) {
              handleIntervalApi();
            }
          }
        })
        .catch(() => {
          error.value = true;
        })
        .finally(() => {
          loading.value = false;
        });
    };

    onMounted(() => {
      onComponentUpdate();
    });

    onBeforeUnmount(() => {
      clearTimeout(intervalFetch);
    });

    return {
      ...useDerivedProps(),
      ...useInitApi(props, proxy),
      shouldComponentUpdate,
      onComponentUpdate,
      loading,
      state,
      error
    };
  },
});
</script>
